import React from 'react';
import {XYPlot, XAxis, YAxis, VerticalGridLines, HorizontalGridLines, VerticalBarSeries} from 'react-vis';

const Chart = (props) => {
        return (
            <XYPlot
                xType="ordinal"    
                width={3000}
                height={400}
                xDistance={100}>
                <VerticalGridLines />
                <HorizontalGridLines />
                <XAxis title="" />
                <YAxis title="" />
                <VerticalBarSeries
                    data={props.data[0]}/>
                <VerticalBarSeries
                    data={props.data[1]}/>
                <VerticalBarSeries
                    data={props.data[2]}/>
            </XYPlot>
        );
}
export default Chart;


