import React, { Component } from 'react';
import './App.css';
import Chart from "./components/chart"

const API_URL = "https://whiteyin.github.io/introduction/state/state.json";
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      results:[]
    }
  }

  componentDidMount() {
    fetch(API_URL).then(response => {
      if (response.ok) {
        return response.json()
      } else {
        throw new Error('something went wrong');
      }
    }).then(response => {
      let newResult = [];
      for (let key in response) {
        if (response.hasOwnProperty(key)) {
          newResult.push(response[key]);
        }
      }
      let data = [[],[],[]];
      let i = 0;
      newResult.map(item => {
        for (let key in item) {
          if (item.hasOwnProperty(key)) {
            let obj = {};
            obj.x = key;
            obj.y = item[key];
            data[i].push(obj);
          }
        }
        i++;
        return undefined;
      })
      this.setState({
        results: data
      });
    });
  }
  render() {
    const { results } = this.state;
    return (
      <div className="APP">
        <Chart data={results} />  
      </div>
    );
  }
}

export default App;



